
@font-face {
  font-family: 'Walkway';
  src: local('Walkway'), url(assets/fonts/Walkway_bold.woff) format('woff');
  font-weight: bold;
  font-style: normal;
  font-display: swap;
}
body{
  background: #000;
  overflow-x: hidden;
  margin: 0;
}
.MuiDrawer-paper.MuiPaper-root{
  -webkit-backdrop-filter: blur(10px);
  backdrop-filter: blur(10px);
  background-color: #257ce173;
}
.MuiMenuItem-root{
  color: #fff;
  font-size: 12px;
  font-family: Montserrat, sans-serif;
  font-weight: 700;
  letter-spacing: 4px;
}